.home-content-1 {
  width: 100%;
  height: 250px;
  background: #f0f0f0;
  > div {
    width: 1140px;
    height: 100%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .btn-wrap {
      width: 405px;
      height: 45px;
      background: #fff;
      border-radius: 100px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 5px 0 24px;
      input {
        width: 270px;
      }
      button {
        width: 93px;
        height: 39px;
        border-radius: 100px;
      }
    }
  }
}
.home-content-2 {
  display: flex;
  justify-content: space-between;
  @media screen and (max-width: 768px) {
    flex-wrap: wrap;
  }
  > div {
    @media screen and (max-width: 768px) {
      width: 80%;
      margin: auto;
      margin-top: 55px;
      margin-bottom: 0;
      &:first-child {
        margin-top: 0;
      }
    }
    margin-bottom: 40px;
    width: 240px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    > div,
    a {
      text-align: center;
    }
    .img-wrap {
      height: 55px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 30px;
      img {
        height: 50px;
      }
    }
    .grey-color {
      margin: 10px 0 25px;
    }
    a {
      width: fit-content;
      margin: auto;
    }
  }
}

.home-content-4 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .homeAboutbg {
    width: 100%;
    height: 689px;
    position: relative;
    background: url(../../assets/imgs/imgs/Aboutcoffeecloud.png) center / cover
      no-repeat;
    @media (max-width: 768px) {
      height: 480px;
      > div {
        position: relative !important;
        transform: translate(0, 0) !important;
        top: 0 !important;
        left: 0 !important;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        overflow: hidden;
        span {
          position: relative;
          z-index: 66;
        }
        img {
          top: auto !important;
          right: 34px !important;
          bottom: -130px !important;
          z-index: 0;
        }
      }
    }
  }
  .btn-wrap {
    display: flex;
    align-items: center;
    button {
      width: 140px;
      height: 40px;
      border-radius: 50px;
      background: #f0f0f0;
      color: #ec407a;
    }
  }
}

.font-55 {
  font-size: 55px;
  line-height: 60px;
}

.enquire-btn {
  width: 100%;
  height: 40px;
  border-radius: 50px;
  background: #fff;
  color: #ec407a;
  margin-top: 50px;
}

.mobile-home-about {
  width: 100%;
  height: 350px;
  position: absolute;
  top: -330px;
}

.contact-wrap {
  @media (max-width: 768px) {
    width: calc(100% - 40px) !important;
    text-align: center;
    font-size: 32px !important;
  }
}
